import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {createGetPricesForPeriodSelector} from "../selectors";
import {getConsentsWithBonuses} from 'eshop/modules/checkout/selectors';
import _ from 'lodash';
import {Section, Trigger} from "eshop/modules/core/components/ui/Expander";
import {maleOrdinal} from "./Utils";

class SummaryDetailsTable extends Component {
    render() {
        let title = this.computeTitle(this.props.entry.monthFrom, this.props.entry.monthTo);
        return (
            <Section header={this.getHeaderView(title)} variant="section" headerClassName="opl-section__header--with-summary u-small-no-padding u-no-border u-padding-left-l-xl"
                summaryClassName="opl-section__border-bottom" summary={this.getSummaryView(title)} className={this.props.className}>
                <table className="u-no-margin-t opl-table opl-conf-summary-table opl-table--50-50 opl-table--no-border-top">
                    <caption className="u-acc-hide">{title}</caption>
                    <thead className="u-acc-hide">
                        <tr>
                            <th></th>
                            <th></th>
                            <th>{this.props.oneTimeHeader}</th>
                            <th>{this.props.monthlyHeader}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.props.prices.map((price, index) => this.prepareTableRow(price, index))}
                    </tbody>
                </table>
            </Section>
        );
    }

    getHeaderView(title) {
        let tooltip = this.computeTooltip(this.props.entry.monthFrom, this.props.entry.monthTo);

        const tooltipStyle = {
            position: 'relative',
            zIndex: 1
        };

        return (
            <div className="l-row">
                <div id={this.props.id} className="l-col l-col-12">
                    <h3 className="h5 g-brand2-c opl-section__heading u-inline u-margin-right">{title}</h3>
                    {(tooltip) ? <span data-type="no-click" data-tooltip-trigger-event="mouseover" className="g-icon g-icon--hint g-icon--font g-icon--xs g-brand2-c o-tooltip__trigger u-inline-block" style={tooltipStyle}></span> : ''}
                    {(tooltip) ? <span className="o-tooltip__content">{tooltip}</span> : ''}
                    <Trigger className="g-brand2-c">
                        <span className="opl-section__text-show">rozwiń</span>
                        <span className="opl-section__text-hide">zwiń</span>
                    </Trigger>
                </div>
            </div>
        );
    }

    getSummaryView(title) {
        const hideElementOnMobile = " td-null";
        const textClass = "h5 g-brand2-c u-no-margin";

        let monthlyPrice = (this.props.entry.monthlyPrice) ?
            <span className={textClass}>{this.formatPrice(this.props.entry.monthlyPrice, this.props.entry.currency)}</span> : <span>&mdash;</span>;

        let oneTimePrice = (this.props.entry.oneTimePrice) ?
            <span className={textClass}>{this.props.entry.oneTimePrice}</span> : <span>&mdash;</span>;

        return (
            <div className="opl-section__summary u-small-padding-top-s">
                <table role="presentation" className="opl-table">
                    <caption>{title + " - skrót"}</caption>
                    <thead>
                        <tr>
                            <th>{this.props.oneTimeHeader}</th>
                            <th>{this.props.monthlyHeader}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr className="u-small-no-padding">
                            <td data-title={this.props.oneTimeHeader} className={"second-last u-small-no-padding g-brand2-c u-no-border" + (this.props.entry.oneTimePrice ? "" : hideElementOnMobile)}>
                                {oneTimePrice}
                            </td>
                            <td data-title={this.props.monthlyHeader} className={"last g-brand2-c u-no-border" + (this.props.entry.monthlyPrice ? "" : hideElementOnMobile)}>
                                {monthlyPrice}
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        );
    }

    isInPeriod(monthFrom, monthTo) {
        return monthFrom <= this.props.entry.monthFrom && (monthTo === null || (this.props.entry.monthTo !== null && monthTo >= this.props.entry.monthTo));
    }

    formatPrice(monthlyPrice, currency) {
        let finalPrice = monthlyPrice;
        return finalPrice.toFixed(2).replace(".", ",") + " " + currency;
    }

    prepareTableRow(price, index) {
        const hideElementOnMobile = " td-null";
        const textClass = "h5 u-no-margin u-font-normal";

        let monthlyPrice = (price.monthlyPrice) ?
            <span className={textClass}>{price.monthlyPrice}</span> : <span>&mdash;</span>;

        let oneTimePrice = (price.oneTimeCost) ?
            <span className={textClass}>{price.oneTimeCost.replace(".", ",")}</span> : <span>&mdash;</span>;
        return (
            <tr key={"price" + index} className="u-small-no-spacing-top">
                <td className="u-w-auto u-font-normal" dangerouslySetInnerHTML={{__html: price.description}}/>
                <td data-title={this.props.oneTimeHeader}
                    className={"second-last u-font-normal" + (price.oneTimeCost ? "" : hideElementOnMobile)}>{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader}
                    className={"last u-font-normal" + (price.monthlyPrice ? "" : hideElementOnMobile)}>{monthlyPrice}</td>
            </tr>
        );
    }

    prepareBonusRow(bonus, index) {
        let price = bonus.priceSet ? bonus.priceSet : bonus.priceModification;
        let monthlyPrice = <span className="h5 u-no-margin u-font-normal">
            {"-" + price.toFixed(2).replace(".", ",") + " " + this.props.entry.currency}
        </span>;
        let oneTimePrice = <span>&mdash;</span>;

        return (
            <tr key={"bonus" + index} className="u-small-no-spacing-top">
                <td className="u-w-auto u-font-normal">{bonus.title}</td>
                <td data-title={this.props.oneTimeHeader}
                    className="second-last u-font-normal td-null">{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader}
                    className="last u-font-normal">{monthlyPrice}</td>
            </tr>
        );
    }

    componentDidMount() {
        if ((!this.props.entry.monthFrom && !this.props.entry.monthTo) || (this.props.entry.monthFrom === 1 && this.props.entry.monthTo === 1)) {
            OPL.UI.loadModules(document.getElementById(this.props.id), [{
                path: 'core/modules/tooltips',
                options: {"mouseoverMinWidth":0, "triggerEvent": "mouseover"}
            }]);
        }
    }

    computeTitle(from, to) {
        if (!from && !to) {
            return this.props.checkoutPriceText;
        } else if (from === 1 && to === 1) {
            return this.props.firstMonthPriceText || "W "+maleOrdinal(1)+" miesiącu";
        } else if (from === to) {
            return "W "+maleOrdinal(from)+" miesiącu";
        } else {
            return "Od " + from + ((to) ? " do " + to : "") + " miesiąca";
        }
    }

    computeTooltip(from, to) {
        if (!from && !to) {
            return this.props.checkoutPriceTooltipContent;
        } else if (from === 1 && to === 1) {
            return this.props.firstMonthPriceTooltipContent;
        }
    }
}

SummaryDetailsTable.propTypes = {
    monthlyHeader: PropTypes.string.isRequired,
    oneTimeHeader: PropTypes.string.isRequired,
    checkoutPriceText: PropTypes.string,
    firstMonthPriceText: PropTypes.string,
    id: PropTypes.string,
    checkoutPriceTooltipContent: PropTypes.string,
    firstMonthPriceTooltipContent: PropTypes.string
};

SummaryDetailsTable.defaultProps = {
    checkoutPriceText: 'Opłata na start',
    firstMonthPriceText: "W pierwszym miesiącu",
    highlighted: false
};

const mapStateToProps = () => {
    const getPricesForPeriod = createGetPricesForPeriodSelector();

    return (state, props) => ({
        prices: getPricesForPeriod(state, props),
        bonusConsents: getConsentsWithBonuses(state)
    });
};

export default connect(mapStateToProps)(SummaryDetailsTable);